import React from "react";
import { ScrollableLink } from "react-update-url-on-scroll";
export default () => {
  document.addEventListener("DOMContentLoaded", () => {
    // Get all "navbar-burger" elements
    const $navbarBurgers = Array.prototype.slice.call(
      document.querySelectorAll(".navbar-burger"),
      0
    );

    // Check if there are any navbar burgers
    if ($navbarBurgers.length > 0) {
      // Add a click event on each of them
      $navbarBurgers.forEach(el => {
        el.addEventListener("click", () => {
          // Get the target from the "data-target" attribute
          const target = el.dataset.target;
          const $target = document.getElementById(target);

          // Toggle the "is-active" class on both the "navbar-burger" and the "navbar-menu"
          el.classList.toggle("is-active");
          $target.classList.toggle("is-active");
        });
      });

      const $menuLink = document.querySelectorAll(".navbar-item");
      $menuLink.forEach(el => {
        el.addEventListener("click", () => {
          document.getElementById("ogNavBar").classList.remove("is-active");
          document.getElementById("burgerMenu").classList.remove("is-active");
        });
      });
    }
  });

  return (
    <nav className="navbar" role="navigation" aria-label="main navigation">
      <div className="container">
        <div className="navbar-brand">
          <a
            role="button"
            id="burgerMenu"
            className="navbar-burger"
            aria-label="menu"
            aria-expanded="false"
            data-target="ogNavBar"
          >
            <span aria-hidden="true" />
            <span aria-hidden="true" />
            <span aria-hidden="true" />
          </a>
        </div>
        <div id="ogNavBar" className="navbar-menu">
          <div className="navbar-end">
            <ScrollableLink href="/aboutme">
              <a className="navbar-item" href="#">
                About Me
              </a>
            </ScrollableLink>
            <ScrollableLink href="/skills">
              <a className="navbar-item" href="#">
                Skills
              </a>
            </ScrollableLink>
            <ScrollableLink href="/work">
              <a className="navbar-item" href="#">
                Work
              </a>
            </ScrollableLink>
            <ScrollableLink href="/contact">
              <a className="navbar-item" href="#">
                Let's Talk
              </a>
            </ScrollableLink>
          </div>
        </div>
      </div>
    </nav>
  );
};
